import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';

import { BrowserRouter } from 'react-router-dom';
import { Provider } from 'react-redux';
import { createStore, combineReducers } from 'redux';


let cartstore = []; 

function reducer(state = cartstore, action ){  
    if( action.type === 'addcart' ) {
        let stateindex = state.findIndex( (a) => { return a.id === action.payload.id } );
          if( stateindex >= 0 ){
               alert('해당상품이 이미 장바구니에 있어요.');
               return  state;                  
          } else {
              let copy = [...state];
              copy.push(action.payload);
              return copy;
          }          

    } else if( action.type === 'delete' ){
         let indexnum = state.findIndex( (a) => { return a.id === action.payload } );
         let copy = [...state];
         copy.splice(indexnum,1);
         return copy;

    } else if( action.type === 'minus' ){
        let copy = [...state];
          if( copy[action.index].qunn > 1 ){      
              copy[action.index].qunn--; 
              return copy;  
          } else {
            return state;
          }        

    } else if( action.type === 'plus' ){
         let copy = [...state];
         copy[action.index].qunn++; 
         return copy;  

    } else{
      return state;
    }      
}

let store = createStore( combineReducers({ reducer,}) );


ReactDOM.render(
  <React.StrictMode>
     <BrowserRouter basename="/reactJH">
       <Provider store={store} >
          <App />
       </Provider>
     </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
