 /*eslint-disable*/ 
 import React, { Component, useState, useEffect } from 'react';
 import './App.css';


 function Cinema( props ){
     return(
        <div className="cinema_wrap">
           <h2 className="uptitle">영화제목 제작중</h2>
           <div className="cine_back">
              <img src={props.jinmov[0].large_cover_image} />               
           </div> 
           <div className="cinema_info">             
             <article>
                <h3>영화제목</h3>
                <h4>액션 코미디/미국</h4>
                <h4>2021.05.23  120분</h4>
             </article>
             <div className="small_img">
                 <img src={props.jinmov[0].medium_cover_image} /> 
             </div>
           </div>        
        </div>
     );  
 }
                                  

export default Cinema; 