 import React, { Component, useState, useEffect, useRef } from 'react';
 import './App.css';
//  import disney from './aesset/disney_YouTube.mp4';


function Video ( props ){
    let video_jin = useRef();
    
    useEffect( () => { 
        video_jin.current.classList.toggle('main_video2'); 
      //  console.log('l');
        return () => { }
     }, [] );    
    return(
        <div id="main_video" ref={video_jin} onClick={ () => { props.setonvideo(false); } } >   
            {/* <video autoPlay loop={true} muted={true} controls >
              <source src={disney}></source> 
            </video>                  */}
            <p style={{ color : 'gray'} }>영상이 있지만 사이즈가 커서 업로드가 안되네.....sorry</p>
        </div>
    );
}

export default Video;  